import React from 'react'
import {BsFillPlayFill} from 'react-icons/bs'
function startButton({PICK_A_Letter,setGameStatus}) {
  return (
    <div className='start-button'>
          <p>اھلا و سھلا مرحبا(خوش آمدید)</p>
          <button onClick={()=>{
            setGameStatus("playing")
            PICK_A_Letter()
          }}>
                    <BsFillPlayFill/>
                    ابھی آغاز کریں
          </button>
    </div>
  )
}

export default startButton