import React from 'react'

function OptionButton({name,id,value,setUserAnswer,getClassName,active,setActive,setLabelValue,setOptionDisplay}) {
  return (
   <>
    <input type='radio' name='answer' id={id} value={value} onClick={(e)=>{ 
      setUserAnswer(e.target.value)
      setActive(e.target.value)
    }} />
  <label style={{color: value === parseInt(active) && "white"}} for={id} className={value === parseInt(active) ? `option ${getClassName()}` : "option"} onClick={(e)=>{ setLabelValue(e.target.innerHTML)
setOptionDisplay(false)
  }}>{name}</label>
   </>
  )
}

export default OptionButton