import React from 'react'
import { useState, useEffect } from 'react'
import OptionButton from './OptionButton'
import {Makharij} from '.././data/data'

function Quiz({letter,setUserAnswer,PARFORM_AFFTER_STEPS,getClassName,PICK_A_LETTER}) {
  const [active, setActive] = useState("")
  const [notSelectedError, setNotSelectedError] = useState(false)
  const [labelValue,setLabelValue] = useState("")
  const [optionDisplay, setOptionDisplay] = useState(false)
  useEffect(()=>{
    active !== "" && setNotSelectedError(false)

  })

  return (
          <div className='quiz'>
          <h3 className='quiz-text'>
            حرفِ "{letter}" کس مخرج سے اداکیا جاتا ہے؟
          </h3>
          <div className='option-container'>
            <div onClick={()=> setOptionDisplay(!optionDisplay)} className={labelValue !== "" ? `selector ${getClassName()}` : "selector" } style={{color: labelValue === "" ? "black" : "white"}}>
              {labelValue === "" ? "جواب کا انتخاب کریں" : labelValue}
              <span className='arrow' style={{
                borderColor: labelValue === "" ? !optionDisplay ? "black transparent transparent" : "transparent transparent black transparent" : optionDisplay ? "transparent transparent white transparent" : "white transparent transparent",
                top: optionDisplay ? "18px" : "26px"
                }}></span>
            </div>
          <div className="custom-options" style={{display: optionDisplay ? "block " : "none"}}>
              {
                Makharij.map((name,index)=>{
                  return(
                    <OptionButton name={name} id={index} value={index}setUserAnswer={setUserAnswer}getClassName={getClassName} active={active} setActive={setActive} setLabelValue={setLabelValue} setOptionDisplay={setOptionDisplay}></OptionButton>
                  )
                })
              }
  

</div>
              <div className="error-and-check-button-div">
             <p>{notSelectedError ? "برائے مہربانی جواب کا انتخاب کریں" : ""}</p>
             <button className={`check-button ${getClassName()}`} onClick={()=> {
              if(active !== ""){
                PARFORM_AFFTER_STEPS()
             setTimeout(()=>{
              setActive("")
              setLabelValue("")
              PICK_A_LETTER()
             },2000)
              } else {setNotSelectedError(true)}
            }}>جائزہ لیں</button>
            </div>
              
            
            
          </div>
        </div>
  )
}

export default Quiz